import React, { useState, useEffect } from "react";
import axios from "axios";
import md5 from "md5";
import Pixelify from "./Pixelify";
import debounce from "lodash/debounce";

const apiUrl = 'https://gateway.marvel.com/v1/public/';
const timestamp = new Date().getTime();
const hash = md5(`${timestamp}${process.env.REACT_APP_API_PRIVATE_KEY}${process.env.REACT_APP_API_KEY}`);

function GameList() {
  const [champions, setChampions] = useState([]);
  const [error, setError] = useState(null);
  const [inputValue, setInputValue] = useState("");
  const [result, setResult] = useState(null);
  const [pixelationLevel, setPixelationLevel] = useState(15);

  const debouncedInputChange = debounce((value) => {
    setInputValue(value);
  }, 300);


  const onCheckCharacterName = (characterName) => {
    // Check if the input value matches the character's name
    const isCorrect = characterName === inputValue;
    // Set the result state
    setResult(isCorrect ? "" : "Incorrect");

    if (isCorrect) {
      // If the input is correct, fetch a new random character
      fetchRandomCharacter();
      setInputValue("");
      // Reset the pixelation level
      setPixelationLevel(15);
    } else {
      // Reduce the pixelation level on an incorrect guess
      if (pixelationLevel > 1) {
        setPixelationLevel(pixelationLevel - 2);
      }
    }
  };

  useEffect(() => {
    fetchRandomCharacter();
  }, []);

  const fetchRandomCharacter = async () => {
    const MAX_REQUESTS_PER_DAY = 3000;
    let apiRequestsCount = 0;

    while (apiRequestsCount < MAX_REQUESTS_PER_DAY) {
      try {
        const response = await axios.get(`${apiUrl}characters`, {
          params: {
            ts: timestamp,
            apikey: process.env.REACT_APP_API_KEY,
            hash: hash,
          },
        });

        // Filter out heroes with "image_not_available" in their thumbnail path
        const validChampions = response.data.data.results.filter(
          (champion) => !champion.thumbnail.path.includes('image_not_available')
        );

        // Get a random subset of heroes (e.g., 5 heroes) from the valid champions
        const randomChampions = getRandomSubset(validChampions, 1);

        setChampions(randomChampions);
        apiRequestsCount++;
        break; // Exit the loop after successfully fetching a character
      } catch (error) {
        setError('Error fetching Marvel characters:', error);
        console.error('Error:', error);
        // Handle the error, you might want to retry or display an error message
        // before breaking the loop to prevent excessive API requests.
      }
    }
  };

  // Function to get a random subset from an array
  const getRandomSubset = (arr, size) => {
    const shuffled = [...arr].sort(() => 0.5 - Math.random());
    return shuffled.slice(0, size);
  };
  
  return (
    <div className='main_content'>
      {error ? (
        <p>{error}</p>
      ) : (
        <div>
          {champions.map((champion) => (
            <div key={champion.id}>
              
              {champion.thumbnail && champion.thumbnail.path && !champion.thumbnail.path.includes('image_not_available') && (
                 
                 <Pixelify
                 src={`${champion.thumbnail.path}.${champion.thumbnail.extension}`} // Set the image source
                 pixelSize={pixelationLevel} // Set the pixelation level
                 className='custom-canvas'
               />
              )}
              <form  className='form_container' onSubmit={(e) => e.preventDefault()}>
                <p className="heroe_name">Heroe name:</p>
                <input
                  type="text"
                  className="hero_input"
                  placeholder="Hulk"
                  value={inputValue}
                  onChange={(e) => setInputValue(e.target.value)}
                />
                
              </form>
              <div className="button_container">
              <button onClick={() => onCheckCharacterName(champion.name)}>
                  Check Name
                </button>
                <button onClick={() => setInputValue("")}>
                  Clear
                </button>
                </div>
              <div className="result_container">
              {result && <p className="result">{result}</p>}
              {pixelationLevel === 1 ? <p className="heroe">{champion.name}</p> : ''}
                </div>
            </div>
          ))}
      </div>
      )}
    </div>
  );
}

export default GameList;